.formParent h1 {
    text-align: center;
    padding-top: 12px;
    text-decoration: underline;
}

.login-form {
  width: 20rem;
  max-width: 90%;
  margin: auto;
  display: block;
  margin-top: 40px;
}

.login-form .submit-button {
    text-align: center;
    margin-top: 10px;
}

.login-form .submit-button button {
    margin-left: 3px;
    margin-right: 3px;
}

.form-control:not(:first-child) {
  margin-top: 10px
}

.form-control label,
.form-control input,
.form-control textarea {
  display: block;
  width: 100%;
  margin-bottom: 0.25rem;
  outline: none;
}

.form-control input,
.form-control textarea {
  font: inherit;
  border-radius: 2px;
  padding: 5px;
}

.form-control.displaynone {
  display: none;
}
.buttonn {
    padding: 9px 12px;
    border-radius: 5px;
    background-color: #07393C;
    border: none;
    cursor: pointer;
    color: #FFA400;
    font-weight: 600;
    outline: none;
}

button:disabled {
    background-color: rgba(7, 57, 60, .6);
    color: rgba(255, 164, 0, .6);
    cursor: auto;
}
.form-control {
    padding-top: 2%;
  margin-left: auto;
  margin-right: auto;
  }
  
  .form-control label,
  .form-control input,
  .form-control textarea {
    display: block;
  }
  
  .form-control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .form-control input,
  .form-control textarea {
    width: 100%;
    font: inherit;
    border: 1px solid #ccc;
    background: #f8f8f8;
    padding: 0.15rem 0.25rem;
  }
  
  .form-control input:focus,
  .form-control textarea:focus {
    outline: none;
    background: #ebebeb;
    border-color: #510077;
  }
  
  .form-control--invalid label,
  .form-control--invalid p {
    color: red;
  }
  
  .form-control--invalid input,
  .form-control--invalid textarea {
    border-color: red;
    background: #ffd1d1;
  }
